.social-contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-icon-div {
  padding: 6px;
  cursor: pointer;
  margin: 6px;
}

.social-icon {
  height: 50px;
}

/* media query for tablets and smaller screens */
@media only screen and(max-width:720px) {
  .social-icon {
    height: 32px;
  }
}
